html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%; }

h1, h2, h3, h4, h5, h6, p, ul, ol, li, button, img, a {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  display: block;
  color: inherit; }

ul {
  list-style-position: inside;
  text-align: left; }

svg {
  width: 100%;
  fill: currentColor; }

img {
  width: 100%;
  min-height: 100px;
  border-radius: 3px;
  vertical-align: middle;
  object-fit: cover; }

@media screen and (max-width: 760px) {
  .wrapper {
    width: 100vw;
    min-height: 100vh;
    margin: 0;
    padding: 0; }

  .content {
    text-align: center; } }

@media screen and (max-width: 1200px) and (orientation: landscape) {
  .content {
    width: 460px;
    margin: 0 auto;
    padding: 0; } }
